import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError} from 'rxjs';
import {HttpService} from 'app/providers/http/http.service'; 

@Injectable({
  providedIn: 'root'
})
export class JobPostService {
  private jobPosts = new BehaviorSubject(undefined);
  private jobPost = new BehaviorSubject(undefined);

  constructor(private httpService : HttpService) { 

  }

  //get all job posts
  public fetchJobPosts(){
    this.httpService.get('job-post/lists').subscribe((data)=>{
      this.jobPosts.next(data);
    })
  }

  public fetchJobPost(id){
   return this.httpService.get('job-post/fetch/'+ id)
  }

  public clearJobPosts(){
    this.jobPosts.next(undefined);
  }

  public jobPostSubsciber(){
    return this.jobPosts;
  }

  public fetchModel(){
    return this.httpService.get('job-post/lists');
  }

  public updateJobPost(jobPostData, id){
    return this.httpService.post('job-post/update/' +id, jobPostData);
  }

  public addJobPost(jobPosts) {
    return this.httpService.post('job-post/', jobPosts);
  }

  public deleteJobPost(id: string){
    let confirmed = confirm("Are you sure you want to delete?");
    if(confirmed){
      this.httpService.delete('job-post/delete/' +id).subscribe((data: any) => {
        this.fetchJobPosts();
      })
    }
  }

  getJobPost(id:any){
    return this.httpService.get('job-post/lists/' +id);
  }

  getAssessment(id:any){
    return this.httpService.get('assessment/lists/' +id);
  }

  getEmployer(id:any){
    return this.httpService.get('employer/lists/' +id);
  }

}
