import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from '@angular/router';
import { CandidateService } from 'app/data/candidate/candidate.service';
import { HttpEvent, HttpEventType } from '@angular/common/http';
import { JobApplicationService } from 'app/data/jobApplication/jobApplication.service';
import { Router } from '@angular/router';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  // selector: 'app-add-job-application',
  templateUrl: './add-job-application.component.html',
  styleUrls: ['./add-job-application.component.css']
})
export class AddJobApplicationComponent implements OnInit {
  form: FormGroup;
  private userInfo;
  jobpostdata: any;
  candidate_id: any;
    
  constructor(private jobApplicationService: JobApplicationService, public fb: FormBuilder, public route: ActivatedRoute, public router :Router, private storageServive: StorageService) {
     const id = this.route.snapshot.paramMap.get('id');
     this.jobApplicationService.getJobPostData(id).subscribe((data:any) =>{
      // console.log(data)
      this.jobpostdata = data ? data.data : undefined;
    });
    
  }
  
  ngOnInit(): void {
     this.form = this.fb.group({
      description: [''],
      jobPost_id:this.route.snapshot.paramMap.get('id')
     })
  }
  submitForm() {
    this.userInfo = this.storageServive.get('userInfo');
    // console.log(this.userInfo.user._id);
     this.jobApplicationService.findCandidate(this.userInfo.user._id).subscribe((data:any) =>{
       console.log("fetch canddate");
       console.log(data[0]._id);
      this.candidate_id =  data[0]._id;
      // console.log(this.candidate_id);
    
    // const id = this.route.snapshot.paramMap.get('id');
    this.form.value.candidate_id = this.candidate_id;
    // this.form.value.jobPost_id = id;
    console.log(this.form.value);
    this.jobApplicationService.addJobApplication(
      this.form.value,
      ).subscribe(jobApplication => {
   this.router.navigate(['/'+this.userInfo.user.role.toLowerCase()+'/my-applications'])

   })
  });

  }
  
      ngOnDestroy(){
  // this.jobSubscriber.unsubscribe();
    }
}
