import { Injectable } from '@angular/core';
import { BehaviorSubject} from 'rxjs';
import {HttpService} from 'app/providers/http/http.service';

@Injectable({
  providedIn: 'root'
})
export class CandidateService {
  private candidates = new BehaviorSubject(undefined);

  constructor(private httpService : HttpService) {
    
   }
  public fetchCandidates(){  //will call and get data from server
    this.httpService.get('candidate/lists').subscribe((data)=>{
     this.candidates.next(data);
    })
  }
  public fetchCandidate(id){
    return this.httpService.get('candidate/fetch/'+id)
  }

  public updateCandidate(candidateData, id) {
    return this.httpService.post('candidate/update/'+id, candidateData)
  }

  public removeCandidate(id: string) {
    let confirmed = confirm('Are you sure?');
    if(confirmed) {
      this.httpService.delete('candidate/delete/' + id).subscribe((data : any) => {
        this.fetchCandidates();
       })
    }
  }
  public clearcandidates(){
    this.candidates.next(undefined);
    ///candidates = undefined     --same as assigning
  }

  public candidatesSubscriber()
  {
    return this.candidates;
  }

}






