import { Component, OnInit } from '@angular/core';
import { JobApplicationService } from 'app/data/jobApplication/jobApplication.service';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  selector: 'app-my-applications',
  templateUrl: './my-applications.component.html',
  styleUrls: ['./my-applications.component.css']
})
export class MyApplicationsComponent implements OnInit {
  private jobApplicationSubscriber: any;
  private jobApplicationData: any;
  private userInfo;
  candidate_id: any;
  username: any;

  constructor(private JobApplicationService: JobApplicationService,private storageServive: StorageService) { 
      // this.jobApplicationSubscriber = this.JobApplicationService.jobApplicationSubscriber().subscribe((data: any) => {   this.jobApplicationData = data ? data.data : undefined;    })
      this.userInfo = this.storageServive.get('userInfo');
      this.JobApplicationService.findCandidate(this.userInfo.user._id).subscribe((data:any) =>{
        this.candidate_id =  data[0]._id;
        this.username =  this.userInfo.user.first_name.charAt(0).toUpperCase() + this.userInfo.user.first_name.slice(1);
        // candidate is stored in this.candidate variable
        /// we are passing to method fetchMyapplications
        this.JobApplicationService.fetchMyApplications(this.candidate_id).subscribe((data:any) =>{
             this.jobApplicationData = data ? data.data : undefined;
        });
    });
    this.JobApplicationService.fetchJobApplications();
  }
 
  ngOnInit(): void {
  }
  delete(jobApplication: any){
    this.JobApplicationService.deleteJobApplication(jobApplication);
}
}
