import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from "@angular/forms";
import { EmployerService } from 'app/data/employer/employer.service';
import { HttpEvent, HttpEventType, HttpErrorResponse } from '@angular/common/http';
import { StorageService } from 'app/providers/storage/storage.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Subscriber } from 'rxjs';

@Component({
  selector: 'employer-profile',
  templateUrl: './employer-profile.component.html',
  styleUrls: ['./employer-profile.component.css']
})

export class EmployerProfileComponent implements OnInit, OnDestroy {
  private employerForm: FormGroup;
  private userInfo;
  private messageClass = 'text-error';
  private message;
  employerid = undefined;
  employersData; 
  private sub;
  private subEmp;
  constructor(public fb: FormBuilder,
    private employerService: EmployerService,
    public router: Router, private route: ActivatedRoute, 
    private storageServive: StorageService) {
      this.userInfo = this.storageServive.get('userInfo');
      this.sub = this.route.params.subscribe(params => {
        // this.id = +params['id']; // (+) converts string 'id' to a number
        this.employerid = params['id'];
        if(this.employerid) {
          this.employerService.fetchEmployer(this.employerid).subscribe((response: any)=>{
            // this.employersData.next(data);
            this.employersData = response.data;
              this.employerForm = this.fb.group({
                company_name: new FormControl(this.employersData.employerDetails.company_name, { validators: [Validators.required]}),
                company_location: new FormControl(this.employersData.employerDetails.company_location, { validators: [Validators.required]}),
                company_website: new FormControl(this.employersData.employerDetails.company_website, { validators: [Validators.required]}),
                company_description: new FormControl(this.employersData.employerDetails.company_description, { validators: [Validators.required]}),
                first_name: new FormControl(this.employersData.userDetails.first_name, { validators: [Validators.required]}),
                last_name: new FormControl(this.employersData.userDetails.last_name, { validators: [Validators.required]}),
                email: new FormControl(this.employersData.userDetails.email, { validators: [Validators.required]}),
                phone_number: new FormControl(this.employersData.userDetails.phone_number, { validators: [Validators.required]}),
                date_of_birth: new FormControl(this.employersData.userDetails.date_of_birth, { validators: [Validators.required]})
              })
           })
        }
      });
      this.employerForm = this.fb.group({
        company_name: new FormControl('', { validators: [Validators.required]}),
        company_location: new FormControl('', { validators: [Validators.required]}),
        company_website: new FormControl('', { validators: [Validators.required]}),
        company_description: new FormControl('', { validators: [Validators.required]}),
        first_name: new FormControl('', { validators: [Validators.required]}),
        last_name: new FormControl('', { validators: [Validators.required]}),
        email: new FormControl('', { validators: [Validators.required]}),
        phone_number: new FormControl('', { validators: [Validators.required]}),
        date_of_birth: new FormControl('', { validators: [Validators.required]}),
        role: new FormControl('', { validators: [Validators.required]})
      })
  }

  ngOnInit(): void {
  }

  ngOnDestroy() {
    if(this.sub)
    this.sub.unsubscribe();
    if(this.subEmp)
    this.subEmp.unsubscribe();
  }
  submitForm() {
    this.subEmp = this.employerService.updateEmployer(this.employerForm.value, this.employersData.employerDetails._id).subscribe( response => {
      if(response.status == "success") {
        if(this.userInfo.user.role=='Admin')
          this.router.navigate(['/'+this.userInfo.user.role.toLowerCase()+'/employers']);
        else {
          this.message = response.message;
          this.messageClass = 'text-success'
        }
      } else {
        alert('Something went wrong');
      }
    })
  }
}