import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { StorageService } from '../storage/storage.service';


@Injectable({
  providedIn: 'root'
})
export class HttpService {
   //private host:string = "http://localhost:3000/";
   private host: string = "https://upstaffed-mm.herokuapp.com/";
  //private host:string = "https://upstaffed-humber.herokuapp.com/"; Priyanka
  // private host:string = "https://upstaffed.herokuapp.com/";
  // private httpOptions: HttpParams;
  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
      'Authorization': 'my-auth-token'
    })
  };
  private userInfo;

  constructor(private http: HttpClient, private storage: StorageService) { 
    // this.httpOptions = {};
    this.updateAuth();
    this.userInfo = this.storage.get('userInfo');
  }

  public updateAuth(userData = undefined) {
    let userInfo = userData ? userData : this.storage.get('userInfo');
    if(userInfo && userInfo.token) {
      // console.log(userInfo.token)
      this.httpOptions.headers = new HttpHeaders({
        'Content-Type':  'application/json',
        'Accept': 'application/json',
        'Authorization': 'Bearer ' + userInfo.token
      })
    }
  }

  public getFileUploadHeaders() {
    return {
      reportProgress: true,
      observe: 'events',
      headers : new HttpHeaders({ 
         Accept: 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Authorization': 'Bearer ' + this.userInfo.token
      })
    }
  }

  public get(api_route) {
    return this.http.get(this.host + api_route, this.httpOptions);
  }

  public post(api_route, params, httpOptionalParams = {}) {
    let httpHeadParams = Object.assign(this.httpOptions, httpOptionalParams)
    console.log(httpHeadParams);
    return this.http.post<any>(this.host + api_route, params, httpHeadParams)
  }

  public put(api_route, params, httpOptionalParams = {}) {
    let httpHeadParams = Object.assign(this.httpOptions, httpOptionalParams)
    console.log(httpHeadParams);
    return this.http.put(this.host + api_route, params, httpHeadParams)
  }
  
  public delete(api_route) {
    return this.http.delete(this.host + api_route, this.httpOptions);
  }
}



