import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError} from 'rxjs';
import {HttpService} from 'app/providers/http/http.service';
import { JobApplicationComponent } from 'app/components/jobApplication/jobApplication.component';

@Injectable({
  providedIn: 'root'
})
export class JobApplicationService {
  private jobApplications = new BehaviorSubject(undefined);
  private jobPosts = new BehaviorSubject(undefined);
  
  constructor(private httpService:HttpService) { }

  public fetchJobApplications(){  //will call and get data from server
    this.httpService.get('jobApplication/lists').subscribe((data)=>{
     this.jobApplications.next(data);
    })
  }
  public fetchModel(){
    return this.httpService.get('jobApplication/lists')
  }
  public fetchMyApplications(user_id){
    return this.httpService.get('jobApplication/lists/myapp/' + user_id);
  }
  public findCandidate(user_id){  //pass user  id to get candidate details
    // console.log(user_id);
   return this.httpService.get('jobApplication/candidate/' + user_id)
  }
  public getUserName(user_id){
   return this.httpService.get('jobApplication/user/' + user_id).subscribe((data)=>{
    this.jobApplications.next(data);
    // console.log(data.first_name);
   })
  }
  public fetchJobPosts(){
    this.httpService.get('job-post/lists').subscribe((data)=>{
      this.jobPosts.next(data);
    })
  }
  public addJobApplication(jobApplication) {
    console.log("Service file " );
  console.log(jobApplication);
    return this.httpService.post('jobApplication/', jobApplication)
  }
  public updateJobApplication(jobApplication) {
    console.log("Service file " );
  console.log(jobApplication._id);
    return this.httpService.put('jobApplication/update/' + jobApplication._id, jobApplication);
  }

  public deleteJobApplication(id: string) {
    let confirmed = confirm('Are you sure you want to delete this application?');
    if(confirmed) {
      this.httpService.delete('jobApplication/delete/' + id).subscribe((data : any) => {
        this.fetchJobApplications();
       })
    }
  }
  //get specific application
  getApplication(id:any){
      return this.httpService.get('jobApplication/lists/' + id)
  }
  getJobPostData(id:any){
   return this.httpService.get('job-post/lists/' + id);
    // return this.httpService.get('jobApplication/lists/' + id)
}
public jobApplicationSubscriber()  {
  return this.jobApplications;
}
 
  public clearJobApplication(){
    this.jobApplications.next(undefined);
    ///same as {jobApplications = undefined}
  }


}
