import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { HttpService } from 'app/providers/http/http.service';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private users = new BehaviorSubject(undefined);
  constructor(private httpService: HttpService) { 
  }

  public fetchStudents() {
    this.httpService.get('user/list').subscribe((data)=>{
      this.users.next(data);
    })
  }

  public addUser(userData) {
    return this.httpService.post('user/register', userData)
  }

  public authenticate(loginData) {
    return this.httpService.post('user/authenticate', loginData)
  }

  public clearStudents() {
    this.users.next(undefined)
  }

  public usersSubscriber(){
    return this.users;
  }
}
