import { Component, OnInit } from '@angular/core';
import { CandidateService } from 'app/data/candidate/candidate.service';
import { Router } from '@angular/router';
import { data } from 'jquery';

@Component({
  selector: 'app-candidate',
  templateUrl: './candidate.component.html',
  styleUrls: ['./candidate.component.css']
})
export class CandidateComponent implements OnInit {
  private candidateData: any;
  private candidateSubscribe: any;

  constructor(private candidateService: CandidateService, private router: Router) { 
    this.candidateSubscribe = this.candidateService.candidatesSubscriber().subscribe((data: any)=>{
      this.candidateData = data ? data.data : undefined;
    })
    this.candidateService.fetchCandidates();
    
  }

  ngOnInit(): void {
  }
  onClickMe(id : any) {
    this.candidateService.removeCandidate(id);
  }
  ngOnDestroy() {
    this.candidateSubscribe.unsubscribe();
  }
  editCandidate(candidateID) {
    console.log(candidateID);
    this.router.navigate(['admin/candidate-profile', candidateID]);
  }

}




