import { Injectable } from '@angular/core';
import { BehaviorSubject} from 'rxjs';
import {HttpService} from 'app/providers/http/http.service';
@Injectable({
  providedIn: 'root'
})
export class EmployerService {
 
  private employers = new BehaviorSubject(undefined);
  constructor(private httpService : HttpService) {

   }

  public fetchEmployers(){
    this.httpService.get('employer/lists').subscribe((data)=>{
     this.employers.next(data);
    })
  }

  public fetchEmployer(id){
    return this.httpService.get('employer/fetch/'+id)
  }

  public updateEmployer(employerData, id) {
    return this.httpService.post('employer/update/'+id, employerData)
  }
  public removeEmployer(id: string) {
    let confirmed = confirm('Are you sure?');
    if(confirmed) {
      this.httpService.delete('employer/delete/' + id).subscribe((data : any) => {
        this.fetchEmployers();
       })
    }
  }

  public clearEmployer(){
    this.employers.next(undefined);
  }

  public employerSubscriber()
  {
    return this.employers;
  }
}
