import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from 'app/data/user/user.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  private registerForm: FormGroup;
  private messageShow;
  private responseMessage;
  constructor(public fb: FormBuilder, private userService: UserService) {
    this.registerForm = this.fb.group({
      first_name: new FormControl('Het', { validators: [Validators.required]}),
      last_name: new FormControl('Kansara', { validators: [Validators.required]}),
      email: new FormControl('het@gmail.com', { validators: [Validators.required]}),
      user_password: new FormControl('1234', { validators: [Validators.required]}),
      confirm_password: new FormControl('1234', { validators: [Validators.required]}),
      phone_number: new FormControl('4564654655', { validators: [Validators.required]}),
      date_of_birth: new FormControl('1996-06-19', { validators: [Validators.required]}),
      role: new FormControl('Candidate', { validators: [Validators.required]})
    })
   }

  ngOnInit(): void {
  }

  private submitForm(values) {
    this.userService.addUser(values).subscribe((response: any)=>{
      if(response) {
        this.messageShow = response.status;
        this.responseMessage = response.message;
        if(response.status == "success") {
          this.registerForm.reset(); 
        }
      }
    });
  }
}
