import { Component, OnInit, OnDestroy } from "@angular/core";
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators,
} from "@angular/forms";
import { CandidateService } from "app/data/candidate/candidate.service";
import {
  HttpEvent,
  HttpEventType,
  HttpErrorResponse,
} from "@angular/common/http";
import { StorageService } from "app/providers/storage/storage.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Subscriber } from "rxjs";
import { EducationService } from "app/data/education/education.service";
import { WorkexperienceService } from "app/data/workexperience/workexperience.service";

@Component({
  selector: "candidate-profile",
  templateUrl: "./candidate-profile.component.html",
  styleUrls: ["./candidate-profile.component.css"],
})
export class CandidateProfileComponent implements OnInit, OnDestroy {
  private candidateForm: FormGroup;
  private educationForm: FormGroup;
  private workexperienceForm: FormGroup;
  private userInfo;
  candidateid = undefined;
  candidateData;
  private newSkill = "";
  private sub;
  private subCan;
  private educationSubscriber;
  private educationData;
  private workexperienceSubscriber;
  private workexperienceData;
  constructor(
    public fb: FormBuilder,
    private candidateService: CandidateService,
    public router: Router,
    private route: ActivatedRoute,
    private storageServive: StorageService,
    private educationService: EducationService,
    private workexperienceService: WorkexperienceService
  ) {
    this.userInfo = this.storageServive.get("userInfo");
    this.sub = this.route.params.subscribe((params) => {
      this.candidateid = params["id"];
      if (this.candidateid) {
        this.candidateService
          .fetchCandidate(this.candidateid)
          .subscribe((response: any) => {
            this.candidateData = response.data;
            this.candidateData.candidateDetails.skills = this.candidateData
              .candidateDetails.skills
              ? this.candidateData.candidateDetails.skills
              : [];
            this.candidateForm = this.fb.group({
              resume_link: new FormControl(
                this.candidateData.candidateDetails.resume_link,
                { validators: [Validators.required] }
              ),
              bio: new FormControl(this.candidateData.candidateDetails.bio, {
                validators: [Validators.required],
              }),
              skills: new FormControl(
                this.candidateData.candidateDetails.skills,
                { validators: [Validators.required] }
              ),
              first_name: new FormControl(
                this.candidateData.userDetails.first_name,
                { validators: [Validators.required] }
              ),
              last_name: new FormControl(
                this.candidateData.userDetails.last_name,
                { validators: [Validators.required] }
              ),
              email: new FormControl(this.candidateData.userDetails.email, {
                validators: [Validators.required],
              }),
              phone_number: new FormControl(
                this.candidateData.userDetails.phone_number,
                { validators: [Validators.required] }
              ),
              date_of_birth: new FormControl(
                this.candidateData.userDetails.date_of_birth,
                { validators: [Validators.required] }
              ),
            });
          });
        this.educationService.fetchEducation(this.candidateid);
        this.workexperienceService.fetchWorkExperience(this.candidateid);
      }
    });
    this.candidateForm = this.fb.group({
      resume_link: new FormControl("", { validators: [Validators.required] }),
      bio: new FormControl("", { validators: [Validators.required] }),
      skills: new FormControl("", { validators: [Validators.required] }),
      first_name: new FormControl("", { validators: [Validators.required] }),
      last_name: new FormControl("", { validators: [Validators.required] }),
      email: new FormControl("", { validators: [Validators.required] }),
      phone_number: new FormControl("", { validators: [Validators.required] }),
      date_of_birth: new FormControl("", { validators: [Validators.required] }),
      role: new FormControl("", { validators: [Validators.required] }),
    });
    this.educationForm = this.fb.group({
      certificate_name: new FormControl("", {
        validators: [Validators.required],
      }),
      institute: new FormControl("", { validators: [Validators.required] }),
      passing_year: new FormControl("", { validators: [Validators.required] }),
    });

    this.educationSubscriber = this.educationService
      .educationSubscriber()
      .subscribe((data: any) => {
        this.educationData = data ? data.data : undefined;
        console.log(this.educationData);
      });

    this.workexperienceForm = this.fb.group({
      job_title: new FormControl("", { validators: [Validators.required] }),
      company_name: new FormControl("", { validators: [Validators.required] }),
      description: new FormControl("", { validators: [Validators.required] }),
      start_date: new FormControl("", { validators: [Validators.required] }),
      end_date: new FormControl("", { validators: [Validators.required] }),
    });

    this.workexperienceSubscriber = this.workexperienceService
      .workexperienceSubscriber()
      .subscribe((data: any) => {
        this.workexperienceData = data ? data.data : undefined;
        console.log(this.workexperienceData);
      });
  }

  ngOnInit(): void {}

  ngOnDestroy() {
    if (this.sub) {
      this.sub.unsubscribe();
    }
    if (this.subCan) {
      this.subCan.unsubscribe();
    }
  }
  submitForm() {
    let candidateDetails = this.candidateForm.value;
    Object.assign(candidateDetails, {
      skills: this.candidateData.candidateDetails.skills,
    });
    this.subCan = this.candidateService
      .updateCandidate(
        candidateDetails,
        this.candidateData.candidateDetails._id
      )
      .subscribe((response) => {
        if (response.status == "success") {
          this.router.navigate([
            "/" + this.userInfo.user.role.toLowerCase() + "/candidates",
          ]);
        } else {
          alert("Something went wrong");
        }
      });
  }

  private submitEducationForm() {
    let educationDetails = this.educationForm.value;
    this.educationService
      .addEducation(this.candidateid, educationDetails)
      .subscribe((data) => {
        if (data && data.status == "success") {
          this.educationForm.reset();
          this.educationService.fetchEducation(this.candidateid);
        }
      });
  }

  private deleteEducation(educationID) {
    this.educationService.removeEducation(educationID, this.candidateid);
  }

  private submitWorkExperienceForm() {
    let workexperienceDetails = this.workexperienceForm.value;
    this.workexperienceService
      .addWorkExperience(this.candidateid, workexperienceDetails)
      .subscribe((data) => {
        if (data && data.status == "success") {
          this.workexperienceForm.reset();
          this.workexperienceService.fetchWorkExperience(this.candidateid);
        }
      });
  }

  private deleteWorkExperience(workexperienceID) {
    this.workexperienceService.removeWorkExperience(
      workexperienceID,
      this.candidateid
    );
  }

  private addSkill() {
    this.candidateData.candidateDetails.skills.push(this.newSkill);
    this.newSkill = "";
  }

  private removeSkill(removeSkill) {
    this.candidateData.candidateDetails.skills = this.candidateData.candidateDetails.skills.filter(
      (skill) => {
        if (skill != removeSkill) {
          return true;
        }
      }
    );
  }
}
