import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';
import { HomeComponent } from './components/home/home.component';
import { CandidateLayoutComponent } from './layouts/candidate-layout/candidate-layout.component';
import { EmployerLayoutComponent } from './layouts/employer-layout/employer-layout.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { QRCodeModule } from 'angularx-qrcode';

const routes: Routes =[
  // {
  //   path: '',
  //   redirectTo: 'dashboard',
  //   pathMatch: 'full',
  // }, {
  //   path: '',
  //   component: AdminLayoutComponent,
  //   children: [{
  //     path: '',
  //     loadChildren: './layouts/admin-layout/admin-layout.module#AdminLayoutModule'
  //   }]
  // }
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full',
  },{
    path: 'home',
    component: HomeComponent,
    pathMatch: 'full',
  }, {
    path: 'admin',
    component: AdminLayoutComponent,
    children: [{
      path: '',
      loadChildren: './layouts/admin-layout/admin-layout.module#AdminLayoutModule'
    }]
  }, {
    path: 'candidate',
    component: CandidateLayoutComponent,
    children: [{
      path: '',
      loadChildren: './layouts/candidate-layout/candidate-layout.module#CandidateLayoutModule'
    }]
  }, {
    path: 'employer',
    component: EmployerLayoutComponent,
    children: [{
      path: '',
      loadChildren: './layouts/employer-layout/employer-layout.module#EmployerLayoutModule'
    }]
  }
];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes,{
       useHash: true
    })
  ],
  exports: [],
})
export class AppRoutingModule { }
