import { Component, OnInit, OnDestroy } from '@angular/core';
import { StorageService } from 'app/providers/storage/storage.service';
import { EducationService } from 'app/data/education/education.service';
import { WorkexperienceService } from 'app/data/workexperience/workexperience.service';

@Component({
  selector: 'app-resume',
  templateUrl: './resume.component.html',
  styleUrls: ['./resume.component.css']
})
export class ResumeComponent implements OnInit, OnDestroy {
  private candidateData: any;
  private skills: string[];
  private experience;
  private educations;
  private educationSubscriber;
  private weSubscriber;
  private workExperiences;

  constructor(private storage: StorageService, private educationService: EducationService, private workExperienceService: WorkexperienceService) { 
    let userData = this.storage.get('userInfo');
    this.candidateData = userData.user;
    this.skills = userData.candidate.skills;
    this.educationService.fetchEducation(userData.candidate._id);
    this.educationSubscriber = this.educationService.educationSubscriber().subscribe((response)=>{
      if(response && response.status == 'success') {
        this.educations = response.data;
      }
    });
    this.workExperienceService.fetchWorkExperience(userData.candidate._id);
    this.weSubscriber = this.workExperienceService.workexperienceSubscriber().subscribe((response)=>{
      if(response && response.status == 'success') {
        this.workExperiences = response.data;
        console.log(this.workExperiences);
      }
    });
    
  }

  ngOnInit(): void {
  }

  ngOnDestroy() {
    if(this.educationSubscriber)
      this.educationSubscriber.unsubscribe();
      
    if(this.weSubscriber)
      this.weSubscriber.unsubscribe();
  }

}
