import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { HttpService } from 'app/providers/http/http.service';

@Injectable({
  providedIn: 'root'
})
export class WorkexperienceService {
  private workexperience = new BehaviorSubject(undefined);

  constructor(private httpService : HttpService) {

   }


  public fetchWorkExperience(candidateid){
    this.httpService.get('workexperience/list/'+candidateid).subscribe((data)=>{
     this.workexperience.next(data);
    })
  }

  public addWorkExperience(candidateid, workexperienceData){
    return this.httpService.post('workexperience/add/'+candidateid, workexperienceData);
  }

  public removeWorkExperience(id: string, candidateid: string) {
    let confirmed = confirm('Are you sure?');
    if(confirmed) {
      this.httpService.delete('workexperience/delete/' + id).subscribe((data : any) => {
        this.fetchWorkExperience(candidateid);
       })
    }
  }

  public clearWorkExperience(){
    this.workexperience.next(undefined);
  }

  public workexperienceSubscriber()
  {
    return this.workexperience;
  }
}

