import { Component, OnInit } from '@angular/core';
import { EmployerService } from 'app/data/employer/employer.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-employer',
  templateUrl: './employer.component.html',
  styleUrls: ['./employer.component.css']
})
export class EmployerComponent implements OnInit {
  private employerData: any;
  private employerSubscriber: any;
 
  constructor(private employerService: EmployerService, private router: Router) { 
    this.employerSubscriber = this.employerService.employerSubscriber().subscribe((data: any) => {   
      console.log(data); 
      this.employerData = data ? data.data : undefined ;
      console.log(this.employerData);
    })
   
    this.employerService.fetchEmployers();
  }

  ngOnInit(): void {

  }
  onClickMe(id : any) {
    this.employerService.removeEmployer(id);
  }

  ngOnDestroy() {
    this.employerSubscriber.unsubscribe();
  }

  editEmployer(employerID) {
    console.log(employerID);
    this.router.navigate(['admin/employer-profile', employerID]);
  }

}
