import { Component, OnInit } from '@angular/core';
import { AssessmentService } from 'app/data/assessment/assessment.service';
import { Router } from '@angular/router';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  selector: 'app-assessment',
  templateUrl: './assessment.component.html',
  styleUrls: ['./assessment.component.css']
})
export class AssessmentComponent implements OnInit {
  private assessmentData: any;
  private assessmentSubscriber: any;
  private userInfo: any;

  constructor(private assessmentService: AssessmentService,
    public router: Router, private StorageService: StorageService) {
    

  }

  ngOnInit(): void {
    this.assessmentSubscriber = this.assessmentService.assessmentSubscriber().subscribe((data: any) => {
      console.log(data);
      this.assessmentData = data ? data.data : undefined;
      console.log(this.assessmentData);
    })

    this.assessmentService.fetchAssessments();
    this.userInfo = this.StorageService.get('userInfo');
    console.log("init");
  }

  onClickMe(id: any) {
    this.assessmentService.removeAssessments(id);
  }

  onClickMeToUpdate(id: string) {
    this.router.navigate(['/' + this.userInfo.user.role.toLowerCase() + '/updateassessment', id]);
  }

  ngOnDestroy() {
    console.log("Destroy");
    if(this.assessmentSubscriber){
    this.assessmentSubscriber.unsubscribe();
    }
  }
}
