import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { HttpService } from 'app/providers/http/http.service';

@Injectable({
  providedIn: 'root'
})
export class EducationService {
  private education = new BehaviorSubject(undefined);
  constructor(private httpService : HttpService) {

   }

  public fetchEducation(candidateid){
    this.httpService.get('education/list/'+candidateid).subscribe((data)=>{
     this.education.next(data);
    })
  }

  public addEducation(candidateid, educationData){
    return this.httpService.post('education/add/'+candidateid, educationData);
  }

  public removeEducation(id: string, candidateid: string) {
    let confirmed = confirm('Are you sure?');
    if(confirmed) {
      this.httpService.delete('education/delete/' + id).subscribe((data : any) => {
        this.fetchEducation(candidateid);
       })
    }
  }

  public clearEducation(){
    this.education.next(undefined);
  }

  public educationSubscriber()
  {
    return this.education;
  }
}
