import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { JobPostService } from 'app/data/job-post/job-post.service';
import { AssessmentService } from 'app/data/assessment/assessment.service';
import { EmployerService } from 'app/data/employer/employer.service';
import { HttpEvent, HttpEventType } from '@angular/common/http';
import { Router } from '@angular/router';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  selector: 'app-add-job-post',
  templateUrl: './add-job-post.component.html',
  styleUrls: ['./add-job-post.component.css']
})
export class AddJobPostComponent implements OnInit {

  form: FormGroup;

  private jobPostData: any;
  private jobPostSubscriber: any;
  private assessmentData: any;
  private assessmentSubscriber: any;
  private employerData: any;
  private employerSubscriber: any;
  percentDone: any = 0;
  private userInfo;
  

  constructor(private jobPostService: JobPostService, private assessmentService: AssessmentService, private employerService: EmployerService, public fb: FormBuilder, public router: Router, private storageServive: StorageService) {
   
    this.jobPostSubscriber = this.jobPostService.jobPostSubsciber().subscribe((data: any) => {
      console.log(data);
      this.jobPostData = data ? data.data : undefined;
      console.log(this.jobPostData);
    })
    this.jobPostService.fetchJobPosts();

    // ==== fetch assessment ====
    this.assessmentSubscriber = this.assessmentService.assessmentSubscriber().subscribe((data: any) => {
      this.assessmentData = data ? data.data : undefined;
    })
    this.assessmentService.fetchAssessments();

    // ==== fetch employer ====
    this.employerSubscriber = this.employerService.employerSubscriber().subscribe((data: any) => {
      this.employerData = data ? data.data : undefined;
    })
    this.employerService.fetchEmployers();
    
    this.userInfo = this.storageServive.get('userInfo');
   }

  ngOnInit(): void {
    this.form = this.fb.group({
      title: [''],
      additional_info: [''],
      position: [''],
      post_date: [''],
      post_expiry: [''],
      job_id: [''],
      requirement: [''],
      assessment_id: [''],
      employer_id: ['']
    })
  }

  onSubmit() {
    console.log(this.form.get('assessment_id').value);
    this.jobPostService.addJobPost(this.form.value).subscribe(jobPost => {
      this.router.navigate(['/'+this.userInfo.user.role.toLowerCase()+'/job-posts'])
    })
  }
  onClickMe(id : any) {
    this.jobPostService.deleteJobPost(id);
  }

  ngOnDestroy() {
    this.jobPostSubscriber.unsubscribe();
  }

  
  // submitForm(){
  //   this.jobPostService.addJobPost(
  //     this.form.value.title,
  //     this.form.value.additional_info,
  //     this.form.value.position,
  //     this.form.value.post_date,
  //     this.form.value.post_expiry,
  //     this.form.value.requirement,
  //     this.form.value.job_id,
  //     this.form.get('assessment_id').value,
  //     this.form.value.employer_id
  //   )
  // }
}
