import { Component, OnInit, OnDestroy} from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { JobApplicationService } from 'app/data/jobApplication/jobApplication.service';
import { ActivatedRoute, Router } from '@angular/router';
import { StorageService } from 'app/providers/storage/storage.service';
import { JobPostService } from 'app/data/job-post/job-post.service';
import { AssessmentService } from 'app/data/assessment/assessment.service';
import { EmployerService } from 'app/data/employer/employer.service';



@Component({
  selector: 'app-update-job-post',
  templateUrl: './update-job-post.component.html',
  styleUrls: ['./update-job-post.component.css'],
})

export class UpdateJobPostComponent implements OnInit, OnDestroy
{
  jobPostId = undefined;
  userInfo: any;
  updateJobPostForm: FormGroup;
  jobPostData;
  private sub;
  private assessmentSubscriber: any;
  private assessmentData: any;
  private employerSubscriber: any;
  private employerData: any;

    constructor(public fb: FormBuilder,
      private jobPostService: JobPostService,
      private assessmentService: AssessmentService,
      private employerService: EmployerService,
      public router: Router,
      private route: ActivatedRoute,
      private storageService: StorageService) 
      {
        this.assessmentSubscriber = this.assessmentService.assessmentSubscriber().subscribe((data: any) => {
          this.assessmentData = data ? data.data :undefined;
          console.log(this.jobPostData);
        })
        this.assessmentService.fetchAssessments();

        this.employerSubscriber = this.employerService.employerSubscriber().subscribe((data: any) => {
          this.employerData = data ? data.data : undefined;
        })
        this.employerService.fetchEmployers();

        this.userInfo = this.storageService.get('userInfo');
        this.sub = this.route.params.subscribe(params => 
          {
            this.jobPostId = params['id'];
            if(this.jobPostId)
            {
              this.jobPostService.fetchJobPost(this.jobPostId).subscribe((response: any) =>
              {
                this.jobPostData = response.data;
                this.updateJobPostForm = this.fb.group(
                {
                  title: new FormControl(this.jobPostData.jobPostDetails.title,{validators: [Validators.required]}),
                  additional_info: new FormControl(this.jobPostData.jobPostDetails.additional_info,{validators: [Validators.required]}),
                  position: new FormControl(this.jobPostData.jobPostDetails.position,{validators: [Validators.required]}),
                  post_date: new FormControl(this.jobPostData.jobPostDetails.post_date,{validators: [Validators.required]}),
                  post_expiry: new FormControl(this.jobPostData.jobPostDetails.post_expiry,{validators: [Validators.required]}),
                  requirement: new FormControl(this.jobPostData.jobPostDetails.requirement,{validators: [Validators.required]}),
                  job_id: new FormControl(this.jobPostData.jobPostDetails.job_id,{validators: [Validators.required]}),
                  assessment_id: new FormControl(this.jobPostData.jobPostDetails.assessment_id,{validators: [Validators.required]}),
                  employer_id: new FormControl(this.jobPostData.jobPostDetails.employer_id,{validators: [Validators.required]})
                })
              })
            }
          });

          this.updateJobPostForm = this.fb.group({
            title: new FormControl('',{ validators: [Validators.required]}),
            additional_info: new FormControl('',{validators: [Validators.required]}),
            position: new FormControl('',{validators: [Validators.required]}),
            post_date: new FormControl('',{validators: [Validators.required]}),
            post_expiry: new FormControl('',{validators: [Validators.required]}),
            requirement: new FormControl('',{validators: [Validators.required]}),
            job_id: new FormControl('',{validators: [Validators.required]}),
            assessment_id: new FormControl('',{validators: [Validators.required]}),
            employer_id: new FormControl('',{validators: [Validators.required]}),
            // 4444role: new FormControl('',{validators:[Validators.required]})
          })
      }

    ngOnInit(): void{
    }

    ngOnDestroy(){
      this.sub.unsubscribe();
    }

    updateForm(){
      this.jobPostService.updateJobPost(this.updateJobPostForm.value,this.jobPostData.jobPostDetails._id).subscribe(response =>{
        console.log(response);
        if(response.status == "success"){
          this.router.navigate(['/'+this.userInfo.user.role.toLowerCase()+'/job-posts']);
        }else{
          alert("Something went wrong");
        }
      })
    }

}
