import { Component, OnInit } from '@angular/core';
import { AssessmentService } from 'app/data/assessment/assessment.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

@Component({
  selector: 'app-reviewassessment',
  templateUrl: './reviewassessment.component.html',
  styleUrls: ['./reviewassessment.component.css']
})
export class ReviewassessmentComponent implements OnInit {
  assessmentData: any;
  id: string;
  assessmentSubscriber: any;
  constructor(private assessmentService: AssessmentService,
    public router: Router, private activeRoute: ActivatedRoute) {
    this.assessmentSubscriber = this.assessmentService.submittedAssessmentSubscriber().subscribe((data: any) => {
      //console.log(data);
      this.assessmentData = data ? data.data : undefined;
      console.log("Review Assessment");
      console.log(this.assessmentData);
    })

  }

  ngOnInit(): void {
    this.activeRoute.paramMap.subscribe(params => {
      this.id = params.get('id');
      this.assessmentService.fetchSubmittedAssessment(this.id);
    })
  }

  ngOnDestroy() {
    this.assessmentSubscriber.unsubscribe();
  }
}
