import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { HttpHeaders } from '@angular/common/http';
import { HttpService } from 'app/providers/http/http.service';
import { StorageService } from 'app/providers/storage/storage.service';


@Injectable({
  providedIn: 'root'
})
export class AssessmentService {
  private assessments = new BehaviorSubject(undefined);
  private assessment = new BehaviorSubject(undefined);
  private submittedassessment = new BehaviorSubject(undefined);

  constructor(private httpService: HttpService, private storageService: StorageService) {

  }

  public fetchAssessments() {
    this.httpService.get('assessment/lists').subscribe((data) => {
      this.assessments.next(data);
    })
  }

  public fetchAssessment(id: string) {
    this.httpService.get('assessment/' + id).subscribe((data) => {
      this.assessment.next(data);
    })
  }

  public fetchSubmittedAssessment(id: string) {
    this.httpService.get('candidateassessment/' + id).subscribe((data) => {
      this.submittedassessment.next(data);
    })
  }

  public removeAssessments(id: string) {
    this.httpService.delete('assessment/' + id).subscribe((data: any) => {
      this.fetchAssessments();
    })
  }

  public addAssessment(title: string, description: string, additional_files: File[], employer_id: string) {
    var formData: any = new FormData();
    var count = 1;
    for (const file of additional_files) {
      formData.append('additional_files', file) // file.name is optional
    }
    formData.append("title", title);
    formData.append("description", description);
    //formData.append("additional_files", additional_files);
    formData.append("employer_id", employer_id);
    console.log("formdata " + formData);
    let userInfo = this.storageService.get('userInfo');
    console.log(userInfo)
    return this.httpService.post('assessment/', formData, this.httpService.getFileUploadHeaders())
  }

  public submitAssessment(assessment_id: string, candidate_id: string, assessement_file: File, other_notes: File) {
    var formData: any = new FormData();
    formData.append("assessment_id", assessment_id);
    formData.append("candidate_id", candidate_id);
    formData.append("other_notes", other_notes);
    formData.append("assessement_file", assessement_file);
    return this.httpService.post('candidateassessment/', formData, this.httpService.getFileUploadHeaders())
  }

  public UpdateAssessment(id: string, title: string, description: string, additional_files: File[], employer_id: string, filesTodelete: []) {
    var formData: any = new FormData();
    var count = 1;
    for (const file of additional_files) {
      formData.append('additional_files', file) // file.name is optional
    }
    formData.append("title", title);
    formData.append("filesTodelete", JSON.stringify(filesTodelete));
    formData.append("description", description);
    //formData.append("additional_files", additional_files);
    formData.append("employer_id", employer_id);
    console.log("formdata " + formData);
    let userInfo = this.storageService.get('userInfo');
    console.log(userInfo)
    return this.httpService.put('assessment/' + id, formData, this.httpService.getFileUploadHeaders())
  }

  public clearAssessment() {
    this.assessments.next(undefined);
  }

  public clearOneAssessment() {
    this.assessment.next(undefined);
  }

  public assessmentSubscriber() {
    return this.assessments;
  }

  public assessmentOneSubscriber() {
    return this.assessment;
  }

  public submittedAssessmentSubscriber() {
    return this.submittedassessment;
  }
}
