import { Component, OnInit } from '@angular/core';
import { JobPostService } from 'app/data/job-post/job-post.service';
import { AssessmentService } from 'app/data/assessment/assessment.service';
import { EmployerService } from 'app/data/employer/employer.service';
import { Router, ActivatedRoute, ParamMap} from '@angular/router';
import { StorageService } from 'app/providers/storage/storage.service';
import { param } from 'jquery';

@Component({
  selector: 'app-view-job-post',
  templateUrl: './view-job-post.component.html',
  styleUrls: ['./view-job-post.component.css']
})
export class ViewJobPostComponent implements OnInit {
  public jobPostData: any;
  private id: String;
  private jobPostSubscriber: any;
  private userInfo: any;
  constructor(private jobPostService: JobPostService, public router: Router, private activeRoute: ActivatedRoute, private storageService: StorageService) { 
    // this.jobPostSubscriber = this.jobPostService.jobPostSubsciber().subscribe((data: any) => {
    //   console.log(data)
    //   this.jobPostData = data ? data.data : undefined;
    // })
    

    this.userInfo = this.storageService.get("userInfo");
  }

  ngOnInit(): void {
    //fetching data from job post
    this.activeRoute.paramMap.subscribe(params => {
      this.id = params.get('id');
      this.jobPostService.fetchJobPost(this.id).subscribe((jobPostData: any)=>{
        if(jobPostData && jobPostData.status == 'success' && jobPostData.data) {
          this.jobPostData = jobPostData.data.jobPostDetails;
        }
      })

    })
  }

  ngOnDestory(){
    this.jobPostSubscriber.unsubscribe();
  }

}
