import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from "@angular/forms";
import { AssessmentService } from 'app/data/assessment/assessment.service';
import { FileUploader } from "ng2-file-upload";
import { Router, ActivatedRoute } from '@angular/router';
import { HttpEvent, HttpEventType, HttpErrorResponse } from '@angular/common/http';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  selector: 'app-updateassessment',
  templateUrl: './updateassessment.component.html',
  styleUrls: ['./updateassessment.component.css']
})
export class UpdateassessmentComponent implements OnInit {
  public uploader: FileUploader = new FileUploader({
    isHTML5: true
  });

  files: File[] = [];
  form: FormGroup;
  percentDone: any = 0;
  private assessmentData: any;
  uploadedFiles: any = [];
  removeFiles: any = [];
  private id: string;
  private assessmentSubscriber: any;
  public userInfo;

  constructor(private assessmentService: AssessmentService,
    public fb: FormBuilder,
    public router: Router,
    private activeRoute: ActivatedRoute,
    private storageService: StorageService) {

    this.form = this.fb.group({
      title: [''],
      description: [''],
      additional_files: [null],
      employer_id: ['']
    })

    this.assessmentSubscriber = this.assessmentService.assessmentOneSubscriber().subscribe((data: any) => {
      this.assessmentData = data ? data.data : undefined;
      console.log(this.assessmentData);
      if (this.assessmentData !== undefined) {
        this.form.controls['title'].setValue(this.assessmentData.title);
        this.form.controls['description'].setValue(this.assessmentData.description);
        this.uploadedFiles = this.assessmentData.additional_files;
      }
    })

    this.userInfo = this.storageService.get('userInfo');
  }

  ngOnInit(): void {

    this.activeRoute.paramMap.subscribe(params => {
      this.id = params.get('id');
      this.assessmentService.fetchAssessment(this.id);
    })
  }

  submitForm() {
    this.form.value.employer_id = "5ed92227fd1fd34cec9e336e";
    //this.form.value.employer_id = this.userInfo.user._id;
    console.log("test" + this.form.value.additional_files);

    for (let j = 0; j < this.uploader.queue.length; j++) {
      let data = new FormData();
      let fileItem = this.uploader.queue[j]._file;
      this.files.push(fileItem);
      console.log(fileItem.name);
    }

    this.uploader.clearQueue();

    this.assessmentService.UpdateAssessment(this.id,
      this.form.value.title,
      this.form.value.description,
      this.files,
      this.form.value.employer_id, this.removeFiles
    ).subscribe((event: HttpEvent<any>) => {
      console.log(this.form.value);
      switch (event.type) {
        case HttpEventType.Sent:
          console.log('Request has been made!');
          break;
        case HttpEventType.ResponseHeader:
          console.log('Response header has been received!');
          break;
        case HttpEventType.UploadProgress:
          this.percentDone = Math.round(event.loaded / event.total * 100);
          console.log(`Uploaded! ${this.percentDone}%`);
          break;
        case HttpEventType.Response:
          console.log('Assessment successfully created!', event.body);
          this.percentDone = false;
          this.router.navigate(['/' + this.userInfo.user.role.toLowerCase() + '/assessment']);
      }
    })
  }

  removeFromArray(id: any) {
    this.uploadedFiles.forEach(element => {
      console.log(element._id);
      if (element._id === id) {
        this.removeFiles.push(element);
        console.log("about to delete");
        this.uploadedFiles = this.uploadedFiles.filter(function (item) {
          return item !== element
        });
      }
    });
  }

  ngOnDestroy() {
    this.assessmentSubscriber.unsubscribe();
  }
}
