import { Component, OnInit } from '@angular/core';
import { UserService } from 'app/data/user/user.service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { StorageService } from 'app/providers/storage/storage.service';
import { Router } from '@angular/router';
import { HttpService } from 'app/providers/http/http.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private loginForm: FormGroup;
  private messageShow;
  private responseMessage;
  constructor(public fb: FormBuilder, private userService: UserService, private storage: StorageService, private router: Router, private http: HttpService) { 
    this.loginForm = this.fb.group({
      email: new FormControl('het@gmail.com', { validators: [Validators.required]}),
      user_password: new FormControl('1234', { validators: [Validators.required]}),
    })
  }

  ngOnInit(): void {
  }

  private submitForm(values) {
    this.userService.authenticate(values).subscribe((response: any)=>{  
      if(response) {
        this.messageShow = response.status;
        this.responseMessage = response.message;
        if(response.status == "success") {
          this.loginForm.reset();  
          this.storage.set('userInfo', response.data);
          this.http.updateAuth(response.data);
          setTimeout(()=>{
            if(response.data && response.data.user.role == 'Admin') {
              this.router.navigate(['/admin']);
            }else if(response.data && response.data.user.role == 'Candidate') {
              this.router.navigate(['/candidate']);
            } else if(response.data && response.data.user.role == 'Employer') {
              this.router.navigate(['/employer']);
            }
            // this.router.navigate(['/admin']);
          }, 500);
        }
      }
    });
  }
}
