import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  constructor() { }

  public get(key) {
    return JSON.parse(localStorage.getItem(key));
  }

  public set(key, value) {
    localStorage.setItem(key, JSON.stringify(value));
  }

  public remove(key) {
    localStorage.removeItem(key);
  }
}
