import { Component, OnInit } from '@angular/core';
import { AssessmentService } from 'app/data/assessment/assessment.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  selector: 'app-viewassessment',
  templateUrl: './viewassessment.component.html',
  styleUrls: ['./viewassessment.component.css']
})
export class ViewassessmentComponent implements OnInit {
  public assessmentData: any;
  private id: string;
  private assessmentSubscriber: any;
  private userInfo: any;
  constructor(private assessmentService: AssessmentService,
    public router: Router, private activeRoute: ActivatedRoute, private storageService: StorageService) {
    this.assessmentSubscriber = this.assessmentService.assessmentOneSubscriber().subscribe((data: any) => {
      //console.log(data);
      this.assessmentData = data ? data.data : undefined;
      //console.log(this.assessmentData);
    })

    this.userInfo = this.storageService.get("userInfo");

  }

  ReviewSubmission(id: string) {
    this.router.navigate(['/' + this.userInfo.user.role.toLowerCase() + '/reviewassessment', id]);
  }

  ngOnInit(): void {
    //Fetch assessment Id from the query string parameters
    this.activeRoute.paramMap.subscribe(params => {
      this.id = params.get('id');
      this.assessmentService.fetchAssessment(this.id);
    })

  }
  ngOnDestroy() {
    this.assessmentSubscriber.unsubscribe();
  }
}
