import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { AssessmentService } from "app/data/assessment/assessment.service";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { HttpEvent, HttpEventType, HttpErrorResponse } from '@angular/common/http';
import { StorageService } from 'app/providers/storage/storage.service';

@Component({
  selector: "app-submitassessment",
  templateUrl: "./submitassessment.component.html",
  styleUrls: ["./submitassessment.component.css"],
})
export class SubmitassessmentComponent implements OnInit {
  assessmentData: any;
  id: string;
  assessmentSubscriber: any;
  userInfo: any;
  percentDone: any = 0;
  form: FormGroup;
  file: File;
  constructor(
    private assessmentService: AssessmentService,
    public router: Router,
    public fb: FormBuilder,
    private activeRoute: ActivatedRoute,
    private storageService: StorageService
  ) {
    this.assessmentSubscriber = this.assessmentService
      .assessmentOneSubscriber()
      .subscribe((data: any) => {
        //console.log(data);
        this.assessmentData = data ? data.data : undefined;
        //console.log(this.assessmentData);
      });

    this.userInfo = this.storageService.get('userInfo');
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      shortnote: [""],
      additional_files: [null],
    });
    //Fetch assessment Id from the query string parameters
    this.activeRoute.paramMap.subscribe((params) => {
      this.id = params.get("id");
      this.assessmentService.fetchAssessment(this.id);
    });
  }

  uploadFile(event) {
    this.file = (event.target as HTMLInputElement).files[0];
    // console.log(file);
    // this.form.patchValue({
    //   avatar: file,
    // });
    // this.form.get("additional_files").updateValueAndValidity();
  }

  submitForm() {
    let candidateId = this.userInfo.user._id;
    console.log(this.file);
    this.assessmentService
      .submitAssessment(
        this.id,
        candidateId,
        this.file,
        this.form.value.shortnote
      )
      .subscribe((event: HttpEvent<any>) => {
        console.log(this.form.value);
        switch (event.type) {
          case HttpEventType.Sent:
            console.log("Request has been made!");
            break;
          case HttpEventType.ResponseHeader:
            console.log("Response header has been received!");
            break;
          case HttpEventType.UploadProgress:
            this.percentDone = Math.round((event.loaded / event.total) * 100);
            console.log(`Uploaded! ${this.percentDone}%`);
            break;
          case HttpEventType.Response:
            console.log("Assessment successfully created!", event.body);
            this.percentDone = false;
            this.router.navigate(['/' + this.userInfo.user.role.toLowerCase() + '/job-posts']);
        }
      });
  }

  ngOnDestroy() {
    this.assessmentSubscriber.unsubscribe();
  }
}
